$black: #000;
$dark: #343434;
$whiteblue: #cceaff;
$bluewhite: #79c7ff;
$bluelight: #199af6;
$blue: #0987e1;
$bluemiddle: #1275bc;
$bluedark: #064a7a;
$blueblack: #05273f;
$grayF9: #f9f9f9;
$grayF6: #f6f6f6;
$grayD: #ddd;
$greyC7: #c7c7c7;
$grey4: #444;
$graymiddle: #6a7a8a;
$gray: #686868;
$graylight: #919090;
$red: #d21b1b;
$white: #fff;

$transition: all .3s ease;

$roboto: 'Roboto', sans-serif;
$fira: 'Fira Sans', sans-serif;

@mixin columnsCount($param){
    columns: $param;
    -moz-columns: $param;
    -webkit-columns: $param;
}
@mixin columnsGap($param){
    column-gap: $param;
    -moz-column-gap: $param;
    -webkit-column-gap: $param;
}
@mixin iconSize($param) {
    width: $param;
    height: $param;
}
@mixin gradient($start, $startColor, $endColor){
    background: linear-gradient($start, $startColor, $endColor);
    background: -moz-linear-gradient($start, $startColor, $endColor);
    background: -webkit-linear-gradient($start, $startColor, $endColor);
    background: -o-linear-gradient($start, $startColor, $endColor);
    background: -ms-linear-gradient($start, $startColor, $endColor);
}
@mixin transform($param){
    transform: $param;
    //-webkit-transform: $param;
    //-moz-transform: $param;
}
@mixin flex($ai:'',$jc:'',$fd:'',$fw:'') {
    display: flex;
    display: -ms-flex;
    @if $ai != '' {
        align-items: $ai;
    }
    @if $jc != '' {
        justify-content: $jc;
    }
    @if $fd != '' {
        flex-direction: $fd;
    }
    @if $fw != '' {
        flex-wrap: $fw;
    }
}

$smart: 640px;
$tablet: 768px;
$laptop: 1025px;
$desktop: 1280px;
$desktopHD: 1500px;
$fullSize: 1680px;
@mixin smart {
    @media (min-width: #{$smart}){
        @content;
    }
}
@mixin tablet {
    @media (min-width: #{$tablet}){
        @content;
    }
}
@mixin laptop {
    @media (min-width: #{$laptop}){
        @content;
    }
}
@mixin desktop {
    @media (min-width: #{$desktop}){
        @content;
    }
}
@mixin desktopHD {
    @media (min-width: #{$desktopHD}){
        @content;
    }
}
@mixin fullSize {
    @media (min-width: #{$fullSize}){
        @content;
    }
}