@import "variables.scss";

.adaboutinter{
	text-align: center;
	max-width: 1045px;
	margin: 0 auto;
	padding-bottom: 53px;
	.adinter-img{
		margin-top: -100px;
		padding-bottom: 20px;
	}
	p{
		color: #232323;
		font-size: 17px;
		line-height: 25px;
		font-weight: 400;
		margin-bottom: 0;
		a{
			color: $bluemiddle;
		}
	}
	@media(max-width: 1000px){
		.adinter-img{
			margin-top: -60px;
			img{
				width: 100px;
				height: 100px;
			}
		}
	}
}

.interbtn{
	box-shadow: 0 1px 15px 1px rgba(0, 0, 0, 0.2);
	border-radius: 5px;
	background-color: $bluemiddle;
	background-image: linear-gradient(-62deg, #199af6 0%, #199af6 1%, #0987e1 100%);
	display: inline-block;
	margin: 0 auto;
	padding: 14px 43px;
	color: #ffffff;
	font-family: "Fira Sans", sans-serif;
	font-size: 30px;
	font-weight: 500;
	line-height: 40px;
	text-decoration: none;
	margin-top: 48px;
	transition: all .3s ease;
	&:hover{
		transform: scale(1.1);
	}
	@media(max-width: 767px){
		font-size: 20px;
		line-height: 28px;
		padding: 10px 15px;
	}
}

.adintersertif{
	background-color: #f6f6f6;
	padding-bottom: 102px;
	padding-top: 39px;
	.widthFix{
		padding: 0;
	}
	.adsertif-line{
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		flex-wrap: wrap;
	}
	h2{
		margin-bottom: 67px;
	}
	.sertif__item{
		display: block;
		width: 25%;
		min-height: 388px;
		border-right: 1px solid #f6f6f6;
		box-shadow: 0 0 15px 1px rgba(0, 128, 255, 0.4);
		background-color: #ffffff;
		background-image: linear-gradient(-50deg, #199af6 0%, #0080ff 100%);
		transition: all .3s ease;
		position: relative;
		padding: 53px;
		padding-bottom: 110px;
		min-height: 404px;
		color: #ffffff;
font-family: "Fira Sans",sans-serif;
font-size: 26px;
font-weight: 500;
line-height: 44.75px;
text-decoration: underline;
		&:hover{
			background-image: linear-gradient(-50deg, #199af6 0%, #0080ff 100%);
			box-shadow: 0 0 15px 1px rgba(0, 128, 255, 0.4);
			text-decoration: none;
			.sertif-arrow{
				img{
					transform: rotate(-90deg);
				}
			}
		}
		.sertif-arrow{
			position: absolute;
			display: block;
			left: 53px;
			bottom: 39px;
			transition: all .3 ease;
			img{
				transition: all .3s ease;
				display: block;
				width: 63px;
				height: 45px;
			}
		}
	}
	@media(max-width: 1500px){
		.sertif__item{
			padding: 30px;
			padding-bottom: 80px;
			font-size: 22px;
			line-height: 36px;
			min-height: 300px;
		}
	}
	@media(max-width: 1200px){
		.sertif__item{
				width: 50%;
		}
	}
	@media(max-width: 767px){
		h2{
			margin-bottom: 40px;
		}
		.widthFix{
			padding: 0 20px;
		}
		.sertif__item{
				width: 100%;
				font-size: 18px;
				line-height: 24px;
				padding: 20px;
				padding-bottom: 50px;
				min-width: 220px;
				border: none;
				margin-bottom: 20px;
				min-height: 260px;
				&:last-child{
					margin-bottom: 0;
				}
		}
		.sertif__item .sertif-arrow{
			left: 20px;
		}
		.sertif__item .sertif-arrow img{
			width: 30px;
			height: 24px;
		}
	}
}


.adnews{
	padding-top: 39px;
	padding-bottom: 102px;
	.newsList{
		padding: 0 90px;
		.newsBox{
			width: 30.5%;
		}
	}
	h2{
		margin-bottom: 65px;
		a{
			color: $bluemiddle;
			border-bottom: 2px solid $bluemiddle;
			text-decoration: none;
		}
	}
	@media(max-width: 1200px){
		.newsList{
			padding: 0;
		}
	}
	@media(max-width: 767px){
		padding: 30px 0;
		.newsList{
			.newsBox{
				width: 100%;
			}
		}
	}
}

.admaincolum{
	width: 66%;
	padding-bottom: 20px;
	@media(max-width: 1025px){
		width: 100%;
	}
	h2{
		text-align: left;
	}
	.newsList .newsBox{
		width: 44%;
	}
	@media(max-width: 767px){
		padding-bottom: 30px;
		.newsList .newsBox{
			width: 100%;
		}
	}
}

.adrgith{
	padding: 5px;
	padding-top: 10px;
	box-shadow: 0 1px 40px 1px rgba(0, 0, 0, 0.11);
background-color: #fff;
.adnews__nav{
	margin: 0;
	list-style-type: none;
	li{
		margin: 0;
		a{
			color: $bluemiddle;
			font-family: "Fira Sans", sans-serif;
		font-size: 24px;
		font-weight: 500;
		line-height: 30px;
		text-decoration: underline;
		display: block;
		width: 100%;
		transition: all .3s ease;
		padding: 20px 18px;
		&:hover{
			box-shadow: 0 1px 15px 1px rgba(8, 135, 225, 0.2);
			background-color: #f3f3f3;
			background-image: linear-gradient(-62deg, #199af6 0%, #199af6 1%, #0987e1 100%);
			color: #ffffff;
			text-decoration: none;
		}
		}
		a.active{
			box-shadow: 0 1px 15px 1px rgba(8, 135, 225, 0.2);
			background-color: #f3f3f3;
			background-image: linear-gradient(-62deg, #199af6 0%, #199af6 1%, #0987e1 100%);
			color: #ffffff;
			text-decoration: none;
		}
	}
}
@media(max-width: 1300px){
	.adnews__nav li a{
		padding: 10px;
		padding-left: 20px;
		font-size: 20px;
		line-height: 26px;
	}
	}
	@media(max-width: 1025px){
	margin-bottom: 40px;
	}
	@media(max-width: 767px){
		.adnews__nav li a{
			padding: 10px;
			padding-left: 20px;
			font-size: 15px;
			line-height: 18px;
		}
	}
}

.adtext{
	color: #232323;
	font-size: 16px;
	font-weight: 400;
	line-height: 25px;
	margin-bottom: 30px;
	img{
		max-width: 100%;
		display: block;
		margin-left: -30px;
	}
	@media(max-width: 1050px){
		img{
			margin-left: 0;
		}
	}
}


.adh3{
	color: $bluemiddle;
font-family: "Fira Sans", sans-serif;
font-size: 30px;
font-weight: 500;
line-height: 38px;
margin-bottom: 17px;
@media(max-width: 767px){
	font-size: 17px;
	line-height: 22px;
}
}

.daspan{
	color: $bluemiddle;
	font-weight: 700;
	font-family: Roboto;
	display: block;
	margin-bottom: 23px;
	margin-top: 10px;
}

.ulfire{
	list-style-image: url(../img/ulfire.png);
	margin-left: 30px;

	li{
		color: #232323;
		font-size: 16px;
		font-weight: 400;
		line-height: 25px;
		margin-bottom: 14px;
		padding-left: 5px;
	}
	@media(max-width: 767px){
		margin-left: 15px;
	}
}

.addownload{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.loadLink{
		width: 45%;
		margin-bottom: 50px;
		align-items: flex-start;
	}
	@media(max-width: 1200px){
		.loadLink{
			width: 48%;
		}
	}
	@media(max-width: 767px){
		.loadLink{
			width: 100%;
			margin-bottom: 30px;
		}
	}
}

.adpageContent{
	padding: 60px 0;
	padding-bottom: 100px;
	@media(max-width: 1000px){
		padding: 50px 0;
	}
	@media(max-width: 767px){
		padding: 30px 0;
	}
}

.davideo{
	img{
		display: block;
		max-width: 100%;
		margin-left: -30px;
	}
	video{
		margin-bottom: 30px;
		box-shadow: 0 1px 40px 1px rgba(0, 0, 0, 0.11);
	}
	a{
		color: #0887e1;
		font-family: "Fira Sans";
		font-size: 25px;
		font-weight: 500;
		line-height: 45px;
		text-decoration: underline;
		display: block;
		text-align: center;
		width: 90%;
	}
	@media(max-width: 767px){
		a{
			width: 100%;
			font-size: 18px;
			line-height: 24px;
		}
		img{
			margin-left: 0;
		}
	}
}

.adblankline{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.adblank-item{
		display: flex;
		justify-content: flex-start;
		align-items: start;
		width: 48%;
		margin-bottom: 39px;
		box-shadow: 0 1px 40px 1px rgba(0, 0, 0, 0.11);
		background-color: #ffffff;
		color: $bluemiddle;
		font-family: Roboto;
		font-size: 17px;
		font-weight: 400;
		line-height: 25px;
		text-decoration: underline;
		padding: 12px 9px;
		transition: all .3s ease;
		img{
			display: block;
			width: 124px;
		}
		.dablank-notice{
			padding-top: 5px;
			margin-left: 11px;
			height: 100%;
			display: block;
			padding-bottom: 40px;
			position: relative;
		}
		.adzoom{
			display: block;
			position: absolute;
			left: 0;
			bottom: 0;
			width: 25px;
			height: 24px;
			background-image: url(../img/adzoom.png);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			transition: all .3s ease;
		}
		&:hover{
			background-image: linear-gradient(-62deg, #199af6 0%, #199af6 1%, #0987e1 100%);
			text-decoration: none;
			color: #fff;
			.adzoom{
				background-image: url(../img/adzoom2.png);
			}
		}
	}
	@media(max-width: 1300px){
		.adblank-item{
			font-size: 15px;
			line-height: 22px;
			img{
				width: 100px;
			}
		}
	}
	@media(max-width: 767px){
		.adblank-item{
		 width: 100%;
		}
	}
}

.adblankpage{
	padding-bottom: 100px;
	@media(max-width: 1050px){
		padding-bottom: 60px;
	}
	@media(max-width: 767px){
		padding-bottom: 30px;
	}
}